<template>
  <div id="app">
    <div class="center">

      <button @click="show = true">显示编辑器</button>
      <button @click="show = false">隐藏编辑器</button>
    </div>
    <vue-editor id="editor" useCustomImageHandler @image-added="handleImageAdded" v-model="htmlForEditor" v-if="show">
    </vue-editor>
    <div v-html="htmlForEditor" class="ql-editor"></div>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
import axios from "axios";
export default {
  components: {
    VueEditor
  },
  watch: {
    htmlForEditor: (val) => {
      localStorage.setItem('v', val);
    }
  },
  data() {
    return {
      htmlForEditor: localStorage.getItem("v") ? localStorage.getItem("v") : '',
      show: false,
    };
  },

  methods: {
    handleImageAdded: function (file, Editor, cursorLocation, resetUploader) {
      // An example of using FormData
      // NOTE: Your key could be different such as:
      // formData.append('file', file)

      var formData = new FormData();
      formData.append("image", file);

      axios({
        url: "https://fakeapi.yoursite.com/images",
        method: "POST",
        data: formData
      })
        .then(result => {
          const url = result.data.url; // Get url from response
          Editor.insertEmbed(cursorLocation, "image", url);
          resetUploader();
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style>
.center {
  text-align: center;
  margin-bottom: 10px;
}
</style>


